import React from 'react'
import HeaderHome from './HeaderHome'

const Regras = (props) => {
    return(
        <div>
            <HeaderHome />
            <div className="container">
                <div className="row">
                    <div className="col-lg-12">
                    <h1 style={{color:'#006F83'}}>Regras</h1>
                    <p>Seja bem vindo ao ReactJOBS, criei este serviço para facilitar a vida tanto de quem precisa de um desenvolvedor React quanto quem precisa de um emprego na área! Espero poder ajudar você nessa jornada, mas primeiro temos que deixar as regras claras para não atrapalharmos o trabalho de outras pessoas ok? A aplicação funciona de uma forma bem simples, porém com o tempo vou melhorar a experiência do usuário. As vagas que são postadas ficarão em destaque na home por ordem e nas categorias por ordem também, todas elas serão listadas em categorias e poderão ser acessadas individualmente.</p>
                    <p>Os dados guardados em nosso banco de dados são livres para quem quiser ver, por isso pedimos e vamos manter somente os dados da vaga em si.</p>
                    <p><b>COMO FUNCIONA?</b><br />A empresa insere os dados da vaga em questão, os devs vão acessar a plataforma e procurar pelas vagas, caso lhe agrade deverá entrar em contato com a empresa pelo e-mail cadastrado na vaga.</p>
                    <p><b>POR QUANTO TEMPO UMA VAGA FICA NO AR?</b><br />Por 30 dias. Em geral os usuários procuram mais pela vaga nos 10 primeiros dias de anúncio. Motivo: os candidatos estão sempre de olho nas oportunidades novas e os recrutadores logo encontram a pessoa ideal para a vaga.</p>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Regras


